    padding: 50px 50px 40px;
    border-radius: 3px;
    position: relative;
    overflow: hidden
}

.modal__inner-2:before {
    top: 0;
    background-image: linear-gradient(270deg,gold,gold 23%,#a5c700 27%,#a5c700 48%,#ff8a00 52%,#ff8a00 73%,#66aefd 77%)
